<template>
  <v-app>
    <v-layout row wrap align-center justify-center class="opansans-font ma-0">
      <v-flex xs12 sm12 md6 lg6 xl6 align-center justify-center>
        <!-- <img :src="loginImg" alt="loginLogo" style="max-width:100%"> -->
        <v-img :src="loginImg" alt="loginLogo" class="mx-auto" max-width="60%"></v-img>
      </v-flex>
      <v-flex xs12 sm12 md6 lg6 xl6 align-center justify-center>
        <v-row class="ma-0 justify-center">
          <v-card class="pa-6 cardBoxShadow rounded-lg" min-width="350">
            <form @keyup.enter="login()">
              <span class="fsize24 themeBlue"> {{getBrokerName}}</span>
              <div class="fsize22 mb-6 primaryColor">
                {{getBrokerName}} Admin Dashboard
              </div>
              <div>
                <p class="mb-2 ml-1 fsize14 primaryColor">Username</p>
                <v-text-field v-model.trim="userName" class="fsize11" placeholder="Enter the username" :rules="userNameRules" outlined dense autocomplete></v-text-field>
              </div>
              <div>
                <p class="mb-2 ml-1 fsize14 primaryColor">Password</p>
                <v-text-field v-model="password" placeholder="Enter the password" :rules="passwordRules" class="fsize11" outlined dense :type="passwordShow ? 'text' : 'password'" :append-icon="passwordShow ? 'mdi-eye' : 'mdi-eye-off'" @click:append="passwordShow = !passwordShow" autocomplete></v-text-field>
              </div>
              <v-btn :loading="getLoader" @click="login()" block depressed class="login-btn text-capitalize mt-3 primary" :disabled="!userName || !password">
                Sign In</v-btn>
            </form>
          </v-card>
        </v-row>
      </v-flex>
    </v-layout>
    <footer class="d-flex justify-end ma-4">
      <span class="secondaryColor fsize13">2021 - 2022 Stoneage Solutions. All rights reserved</span>
    </footer>
  </v-app>
</template>


<script>
import loginImg from "../assets/loginPic.png";
import { mapGetters } from "vuex";
export default {
  name: "Login",

  data: () => ({
    loginImg: loginImg,
    userName: "",
    password: "",
    passwordShow: false,
    userNameRules: [
            v => !!v || 'Username is required',
        ],
    passwordRules: [
            v => !!v || 'Password is required',
            v => (v && v.length >= 6) || 'Password must be at least 6 characters'
        ]
  }),

  computed: {
    ...mapGetters(["getBrokerName","getLoader"]),
  },

  created() {
    // localStorage.clear();
  },

  methods: {
    
    login() {
      this.$store.dispatch("adminLogin", {
        userName: this.userName,
        password: this.password,
      });
    },
  }
  
  
};
</script>

